body{
  background-color: rgb(198, 233, 165);
  position: relative;
}
.headerClass{
  text-align: center;
  color: #4B99AD
}

.footerClass{
  text-align: center;
  color: rgb(18, 59, 70)
}
.form-style-2{
	max-width: 500px;
	padding: 20px 12px 10px 20px;
  font: 13px Arial, Helvetica, sans-serif;
  position: relative;
  margin: 50px  auto;
  background-color: rgb(47, 216, 216);
  text-align: left;
  min-height: 300px
}
.form-style-2-heading{
	font-weight: bold;
	font-style: italic;
	border-bottom: 2px solid rgb(80, 34, 34);
	margin-bottom: 20px;
	font-size: 15px;
  padding-bottom: 3px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color:blueviolet
}

.form-style-2-heading2{
	font-weight: bold;
	font-style: italic;
	border-bottom: 2px solid rgb(80, 34, 34);
  margin-bottom: 20px;
  margin-top: 10px;
	font-size: 15px;
  padding-bottom: 3px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color:blueviolet
}
.form-style-2 label{
	display: block;
}
.form-style-2 p{
	background-color: rgb(206, 166, 194)
}
.form-style-2 label > span{
	width: 100px;
	font-weight: bold;
	float: left;
	padding-top: 8px;
	padding-right: 5px;
}

.form-style-2 input.input-field, .form-style-2 .select-field{
  width: 70%;	
  background: rgb(220, 221, 218);
  color: rgb(6, 19, 29)
}
.form-style-2 input.input-field, 
.form-style-2 .textarea-field, 
 .form-style-2 .select-field{
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	border: 1px solid #C2C2C2;
	box-shadow: 1px 1px 4px #EBEBEB;
	-moz-box-shadow: 1px 1px 4px #EBEBEB;
	-webkit-box-shadow: 1px 1px 4px #EBEBEB;
	border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	padding: 7px;
	outline: none;
}
.form-style-2 .input-field:focus, 
.form-style-2 .textarea-field:focus,  
.form-style-2 .select-field:focus{
  border: 1px solid #0C0;
  background: rgb(206, 223, 233);
}
.form-style-2 .textarea-field{
	height:100px;
	width: 55%;
}
.form-style-2 input[type=submit],
.form-style-2 input[type=button]{
	border: none;
	padding: 8px 15px 8px 15px;
	background: rgb(199, 166, 18);
	color: #fff;
	box-shadow: 1px 1px 4px #DADADA;
	-moz-box-shadow: 1px 1px 4px #DADADA;
	-webkit-box-shadow: 1px 1px 4px #DADADA;
	border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
}
.form-style-2 input[type=submit]:hover,
.form-style-2 input[type=button]:hover{
	background: rgb(221, 94, 10);
	color: #fff;
}

 .form-style-2 .list{
  font: 5px Arial, Helvetica, sans-serif;
  background-color: rgb(85, 26, 67);
  width: 87%;
  max-height: 100px;
}
.list p{
  position: relative;
  margin: 10px auto;
  padding:8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color:rgb(15, 1, 1);
  font-size: 10px;
  outline: none;
  text-align: left;
  overflow-wrap: break-word;  
  background-color: rgb(178, 215, 245);
  min-height: 15px;
}
 .button-style{
  background: #4B99AD;
	padding: 1px ;
	border: none;
  color: #fff;
  position: absolute;
  margin-top: 0px;
  margin: 0px auto;
  right:10px
}

.list p input{
  background-color: transparent;
  outline: none;
  border:0;
  color:rgb(8, 1, 1);
  font-size:15px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  width: 85%; 
  top:10px;
  margin-left: 15px;
}

.list p label{
  background-color: transparent;
  outline: none;
  border:0;
  color:rgb(8, 1, 1);
  font-size:15px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  width: 85%;  
  position: relative;
  left:18px; 
  top:1px;
}

::placeholder{
  color: rgb(58, 80, 87);
}
.list p span{
  position: absolute;
  right:15px;
  cursor: pointer;
  margin-top: 0px;
}

.faiconsAfter{
  font-size: 15px;
  margin-top: 0px;
  background:transparent;
	padding: 1px ;
	border: none;
  color: rgb(15, 105, 101);
  position: absolute;
  right: 14px;
  top: 8px;
}
.faiconsBeforeEnd{
  font-size: 15px;
  margin-top: 0px;
  background:transparent;
	padding: 1px ;
	border: none;
  color: rgb(15, 105, 101);
  position: absolute;
  right: 35px;
  top: 8px;
}
.faiconsBefore{
  font-size: 15px;
  margin-top: 0px;
  background:transparent;
	padding: 1px ;
	border: none;
  color: rgb(15, 105, 101);
  position: absolute;
  margin-top: 0px;
  margin: 0px auto;
  left: 2px;
  top:10px;
 
}

